html,
body,
.main {
  height: 100%;
}

body {
  min-width: 980px;
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
}

.main.is-elevate {
  background: url("../img/bg-elevate.jpg") no-repeat center top;
  background-size: cover;
}

.main.is-metro {
  background: #eaeae9;
}

.content {
  padding: 20px;
}

.container {
  position: relative;
  padding: 20px;
  margin: 0 auto;
  max-width: 980px;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

.header {
  height: 70px;
  color: #fff;
  text-align: center;
  font-size: 0;
}

.header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.is-elevate .header {
  background: #2e2e2e;
}

.is-elevate .header .header__logo img {
  max-height: 25px;
}

.is-metro .header {
  background: #3723ad;
}

.is-metro .header .header__logo img {
  max-height: 19px;
}

.header__logo {
  display: inline-block;
  vertical-align: middle;
}

.header__logo img {
  max-height: 28px;
}

.grid {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.grid__header {
  display: table-header-group;
}

.grid__header .grid__col {
  position: relative;
  padding-right: 40px;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.3;
}

.is-elevate .grid__header .grid__col {
  background: #2e2e2e;
}

.is-metro .grid__header .grid__col {
  background: #3723ad;
}

.grid__header .grid__col.is-up:before, .grid__header .grid__col.is-down:before {
  opacity: 1;
}

.grid__header .grid__col.is-up:before {
  transform: rotate(180deg);
}

.grid__header .grid__col:before {
  content: '';
  position: absolute;
  top: 50%;
  right: 20px;
  margin-top: -4px;
  border-style: solid;
  border-width: 8px 6px 0;
  border-color: transparent;
  opacity: 0;
  transition: opacity 0.25s;
}

.is-elevate .grid__header .grid__col:before {
  border-top-color: #EF902D;
}

.is-metro .grid__header .grid__col:before {
  border-top-color: #fff;
}

.grid__body {
  display: table-row-group;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
}

.grid__body .grid__row:not(:last-child) .grid__col {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.grid__row {
  display: table-row;
}

.grid__col {
  display: table-cell;
  padding: 20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.3;
}
