.header {
	height: 70px;
	color: $white;
	@include vertical;
	@include elevate {
		background: $elevateSecondary;
		.header__logo img {
			max-height: 25px; } }
	@include metro {
		background: $metroPrimary;
		.header__logo img {
			max-height: 19px; } }
	&__logo {
		display: inline-block;
		vertical-align: middle;
		img {
			max-height: 28px; } } }
